h1 {
  text-align: center;
  font-size: 5.4rem;
  padding-bottom: 0.75rem;
}

.container {
  width: 100vw;
  align-items: flex-start;
  margin: 0 auto;
  padding: 2rem;
  max-width: 100%;
}

@media screen and (max-width: 800px) {
  h1 {
    font-size: 3rem;
  }
}

@media screen and (max-width: 500px) {
  h1 {
    font-size: 2.5rem;
  }
}
