.cardSize {
    width: 18rem;
    height: 25rem;
    margin-bottom: 1rem;
    transition: 0.3s;
  }
  
  .cardSize:hover {
    box-shadow: 8px 8px 8px var(--accent-color);
    transform: scale(1.05);
  }
  
  .techSub {
    font-size: 0.65rem;
    color: var(--dark-grey);
    margin: 0 0;
  }
  
  .iconGitHub {
    background-image: url("../../Assets/Icons/github-square.svg");
  }
  
  .iconGitHub:hover {
    background-image: url("../../Assets/Icons/Hover/github-square.svg");
  }
  
  .iconFigma {
    background-image: url("../../Assets/Icons/figma.svg");
  }
  
  .iconFigma:hover {
    background-image: url("../../Assets/Icons/Hover/figma.svg");
  }
  
  .iconLink {
    background-image: url("../../Assets/Icons/external-link.svg");
  }
  
  .iconLink:hover {
    background-image: url("../../Assets/Icons/Hover/external-link.svg");
  }
  
  .iconDesc {
    background-image: url("../../Assets/Icons/text.svg");
  }
  
  .iconDesc:hover {
    background-image: url("../../Assets/Icons/Hover/text.svg");
  }
  
  .iconGitHub,
  .iconFigma,
  .iconLink,
  .iconDesc {
    height: 25px;
    width: 25px;
    margin: 0 0.5rem;
    background-repeat: no-repeat;
    display: inline-block;
  }
  
  .hiddenSpan {
    height: 1px;
    width: 1px;
    position: absolute;
    overflow: hidden;
    top: -10px;
  }
  