  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: .1em solid var(--black); /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .1em; /* Adjust as needed */
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation: 
    home_typing__3AE7Q 4s steps(40, end),
    home_blink-caret__35lgT .75s step-end infinite;
          animation: 
    home_typing__3AE7Q 4s steps(40, end),
    home_blink-caret__35lgT .75s step-end infinite;
}

/* The typing effect */
@-webkit-keyframes home_typing__3AE7Q {
  from { width: 0 }
  to { width: 40% }
}
@keyframes home_typing__3AE7Q {
  from { width: 0 }
  to { width: 40% }
}
/* The typewriter cursor effect */
@-webkit-keyframes home_blink-caret__35lgT {
  from, to { border-color: transparent }
  50% { border-color: var(--black); }
}
@keyframes home_blink-caret__35lgT {
  from, to { border-color: transparent }
  50% { border-color: var(--black); }
}

