h3,
h4 {
  margin-bottom: 1rem;
  text-align: center;
}

h4 {
  font-size: 0.9rem;
  font-style: normal;
  margin-bottom: 2rem;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: var(--bg);
  color: var(--black);
  max-width: 100%;
}
/* UTILITIES */

.container {
  width: 100vw;
  max-width: 100%;
  padding: 2rem;
}

.profile {
  border-radius: 50%;
  background-image: url("../../Assets/Prof.png");
  background-size: 25rem;
  margin: 0 auto;
  height: 25rem;
  width: 25rem;
}

.flexed {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  margin-bottom: 5rem;
}

.col {
  width: 50%;
}

.section2 {
  width: 75%;
}

.text {
  display: inline;
}

.textName {
  font-size: 3rem;
}

.typewriter h2 {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: .1em solid var(--black); /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .1em; /* Adjust as needed */
  width: fit-content;
  animation-fill-mode: forwards;
  animation: 
    typing 4s steps(40, end),
    blink-caret .75s step-end infinite;
}

/* The typing effect */
@keyframes typing {
  from { width: 0 }
  to { width: 40% }
}
/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: var(--black); }
}

/* Tablet */
@media screen and (max-width: 1024px) {
  h4 {
    font-size: 1rem;
  }
  .profile {
    height: 15rem;
    width: 15rem;
    background-size: 15rem;
    margin-bottom: 2rem;
  }
  .flexed {
    flex-direction: column;
    margin-bottom: 2rem;
  }
  .col {
    width: 100%;
  }

  .textName {
    font-size: 2rem;
  }
  .typewriter h2 {
    font-size: 2rem;
  }  
}

/* Mobile */
@media screen and (max-width: 480px) {
  @keyframes typing {
    from { width: 0 }
    to { width: 95% }
  }
  
}


